@media screen and (max-width: 599px) {

   /*===================== all =====================*/
   :root {
      font-size: 62.5%;
   }

   .pc {
      display: none;
   }

   .mobile {
      display: block;
   }

   .mobile-page {
      /*font-family: zen-maru-gothic, sans-serif;*/
      font-family: sans-serif;
      font-weight: 500;
      font-style: normal;
      color: var(--text);
      background: linear-gradient(to top, rgba(0, 0, 0, 0.1) 0%, rgba(0, 0, 0, 0.1) 100%),
      url(${___CSS_LOADER_URL_REPLACEMENT_0___}) no-repeat center center/cover;
      background-attachment: fixed;
      position: relative;
      width: 100%;
      height: auto;
      overflow: visible;
   }

   /*===================== fade =====================*/
   .fade_bottom {
      transition: opacity 1.2s ease, transform 1.2s ease, filter 1.2s ease;
      filter: blur(4px);
      transform: translateY(15px);
   }

   .fade_bottom.visible {
      filter: blur(0);
      transform: translateY(0);
   }

   /*===================== border =====================*/
   body::before,
   .hr {
      border-image: none;
   }

   header {
      position: absolute;
   }

   /*===================== body =====================*/
   body::before {
      border: 1.5vh solid;
      z-index: 1000;
      border-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) 100;
      box-shadow: inset 1px 1px 6px 1px rgba(0, 0, 0, 0.9);
   }

   body::after {
      display: none;
      top: 0.5vh;
      left: 1vw;
      width: 98vw;
      height: 99vh;
      border: 3px solid;
      box-shadow: inset 0px 1px 5px 1px rgba(0, 0, 0, 0.5);
      z-index: 1000;
      border-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___}) 100;
   }

   .noise {
      background: linear-gradient(to bottom right, var(--bg), var(--text)) fixed;
      -webkit-background-clip: text;
      -webkit-text-fill-color: var(--text);
      --lines: 0.01px;
      mask: repeating-radial-gradient(circle at center,
            #000,
            var(--lines),
            #000,
            0,
            #0000,
            calc(var(--lines) * 2),
            #0000 0);
      /* フォールバック：マスクがサポートされない場合の背景色 */
      background-color: lightgray;
      /* フォールバック用のテキスト色 */
      /* サポートされない場合の代替設定 */
      background-clip: border-box;
      /* 追加：他のブラウザ用 */
      background-clip: text;
      /* 追加：他のブラウザ用 */
      color: transparent;
      /* 追加：Webkit未対応時にテキストを透明に */
   }

   footer .noise {
      -webkit-text-fill-color: darkgray;
   }

   @media (prefers-color-scheme: light) {
      .noise {
         background: linear-gradient(to bottom, black, gray) fixed;
         -webkit-background-clip: text;
         -webkit-text-fill-color: darkgray;
         /* サポートされない場合の代替設定 */
         background-clip: border-box;
         /* 追加：他のブラウザ用 */
         background-clip: text;
         /* 追加：他のブラウザ用 */
         color: transparent;
         /* 追加：Webkit未対応時にテキストを透明に */
      }
   }

   /*===================== img =====================*/
   img {
      max-width: 100%;
      width: 65%;
   }

   /*===================== img =====================*/

   /*===================== text =====================*/
   h1,
   h2,
   h3,
   h4 {
      background: none;
   }

   h1 {
      /*font-size: 62.5%;=16px 12rem=120px*/
      font-size: clamp(2rem, -0.182rem + 10.91vw, 8rem);
      font-family: "Jost", sans-serif;
      font-optical-sizing: auto;
      font-weight: 500;
      width: fit-content;
      line-height: 1.2;
      letter-spacing: 0.1rem;
      margin-bottom: 1rem;
      color: var(--text);
      mix-blend-mode: overlay;
      padding-left: 10vmin;
   }

   h2,
   h3,
   h4 {
      font-family: "Inter", sans-serif;
      font-optical-sizing: auto;
      font-weight: 400;
      font-style: normal;
      color: var(--text);
   }

   h2 {
      /*font-size: 62.5%;=16px 6rem=62px*/
      font-family: serif;
      font-weight: 500;
      font-size: clamp(2rem, 0.545rem + 7.27vw, 6rem);
      width: fit-content;
      line-height: 1;
   }

   h3 {
      /*font-size: 62.5%;=16px 4rem=42px*/
      font-size: clamp(2rem, -0.286rem + 11.43vw, 4rem);
   }

   h4 {
      /*font-size: 62.5%;=16px 4rem=42px*/
      font-size: clamp(1.8rem, 1.582rem + 1.09vw, 2.4rem);
   }

   p {
      /*font-size: 62.5%;=16px 1.2rem=12px*/
      font-size: clamp(1.4rem, 1.171rem + 1.14vw, 1.6rem);
      width: fit-content;
      letter-spacing: 0.01rem;
      line-height: 2;
   }

   .text {
      padding: 1vh 10vmin;
   }

   ol,
   ul {
      font-size: clamp(1.4rem, 0.943rem + 2.29vw, 1.8rem);
      font-family: sans-serif;
   }

   li {
      font-size: clamp(1.4rem, 1.171rem + 1.14vw, 1.6rem);
      list-style: none;
   }


   /*===================== リンク =====================*/
   a {
      font-size: 1.8rem;
      color: var(--text);
      width: auto;
      height: fit-content;
      padding: 0.25rem 0.5rem;
      position: relative;
      display: block;
      text-decoration: none;
      z-index: 9;
   }

   a:active {
      color: rgba(217, 217, 217, 0.5);
   }

   .link-arrow {
      display: inline-block;
      vertical-align: middle;
      color: #333;
      line-height: 1;
      position: relative;
      width: 1em;
      height: 0.1em;
      background: currentColor;
      transform: rotate(-45deg);
   }

   .link-arrow::before {
      content: '';
      width: 0.65em;
      height: 0.65em;
      border: 0.1em solid currentColor;
      border-left: 0;
      border-bottom: 0;
      transform: rotate(45deg);
      transform-origin: top right;
      position: absolute;
      top: 50%;
      right: -0.05em;
      box-sizing: border-box;
   }

   /*===================== リンク =====================*/

   .hr {
      height: 30px;
      margin: 10vmin 0;
      border-top: 1px solid;
      border-bottom: 1px solid;
      box-shadow: inset 0px 0px 5px 0px #000;
      background-color: rgba(200, 200, 200);
      mix-blend-mode: multiply;
   }

